import $ from 'jquery'

(($) => {
  if (typeof $ === 'undefined') {
    throw new TypeError('CMDB\'s JavaScript requires jQuery. jQuery must be included before CMDB\'s JavaScript.')
  }
})($);

import './cmdb/sidebar.js';
import './cmdb/error.js';
import './cmdb/wizard.js';

// Vendors
import './vendors/pace.js';
import './vendors/quicksearch.js';
import './vendors/datatables/datatables.js';
import './vendors/jquery-qrcode.js';
// import './vendors/form-builder.js';
// import './vendors/form-render.js';

$(document).ready(function() {

  $('.nav-dropdown-toggle').on('click', function(e) {
    e.preventDefault();
    $(this).parent().toggleClass('open');
  });

  $('ul.nav').find('a.active').parent().parent().parent().addClass('open');
  $('.sidebar-toggle').sidebar();
  $('input#category_filter').quicksearch('#sidebar .sidebar-categories .nav-link');

  $('.data_table').each(function() {
    $(this).DataTable();
  });
  $('.wizard').each(function () {
      $(this).wizard();
    });
});
